export const query = graphql`
    query {
        nohrdBike: file(relativePath: {eq: "portfolio/nohrd-bike/nohrd-bike.jpg"}) {
            ...fullscreenHero
        }
        joris: file(relativePath: {eq: "people/joris_2_zoom.jpg"}) {
            ...inlineAvatarImage
        }
        nohrdBikeDevices: file(relativePath: {eq: "portfolio/nohrd-bike/nohrd-bike-devices.png"}) {
            ...fullscreenHero
        }
        nohrdBikeBomb: file(relativePath: {eq: "portfolio/nohrd-bike/nohrd-bike-bomb.jpg"}) {
            ...fluid800Width
        }
        nohrdIPhones: file(relativePath: {eq: "portfolio/nohrd-bike/nohrd-bike-iphones.png"}) {
            ...fluid800Width
        }
        nohrdBikeVideo: file(relativePath: {eq: "portfolio/nohrd-bike/nohrd-bike-video.jpg"}) {
            ...fluid1200Width
        }
        nohrdBikeRoom: file(relativePath: {eq: "portfolio/nohrd-bike/nohrd-bike-room.jpg"}) {
            ...fluid1200Width
        }
    }
`;
